<template>
    <v-container style="height: 100%; position: relative; width: 100%" align="center">
        <v-row>
            <v-col cols="12">
                <h2>{{chooseYourProfileImageText}}</h2>
            </v-col>
        </v-row>

        <v-row style="height: 100%">
            <v-col cols="12"
                   sm="6" offset-sm="3"
                   md="4" offset-md="4"
                   style="height: 100%">
                <v-card
                        align="center"
                >
                    <v-progress-linear
                            value="100"
                            height="10px"
                            color="blue-grey"
                            dark
                    >
                    </v-progress-linear>

                    <v-container>
                        <profile-image-selector
                                size="1"
                                @newImageSelected="newImageSelected($event)">
                        </profile-image-selector>

                        <v-btn block color="primary" @click="uploadUserProfileImage" :disabled="buttonIsDisabled">
                            {{continueText}}
                        </v-btn>
                    </v-container>
                </v-card>
            </v-col>
        </v-row>

        <!--SnackBar-->
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
        <!--End SnackBar-->
    </v-container>

</template>

<script>
    import Constants from "../../store/Constants";
    import {mapActions} from 'vuex';
    import ProfileImageSelector from "../AuxInputComponents/ProfileImageSelector";

    export default {
        name: "SetYourProfileImage",
        components: {ProfileImageSelector},
        data() {
            return {
                showImg: true,
                imagePreview: this.$store.getters['user/getGenderDefaultProfileImgByUserGender'],
                imageUpload: null,
                snackbar: false,
                snackbarText: '',
                chooseYourProfileImageText: Constants.TEXT_CHOOSE_YOUR_PROFILE_IMAGE,
                closeText: Constants.TEXT_CLOSE,
                continueText: Constants.TEXT_TO_CONTINUE,
            }
        },
        methods: {
            ...mapActions('user', ['setProfileImg', 'setRegistrationCompleted']),
            newImageSelected: function (image) {
                this.imageUpload = image;
            },

            uploadUserProfileImage() {
                let self = this;

                if (self.imageUpload) {
                    let resultSetProfileImg = this.setProfileImg(self.imageUpload);

                    resultSetProfileImg.then(() => {
                        let resultSetRegistrationCompleted = this.setRegistrationCompleted(self.imageUpload);

                        resultSetRegistrationCompleted.then(() => {
                            this.$router.replace('dashboard');
                        })
                            .catch(function (error) {
                                self.snackbarText = error;
                                self.snackbar = true;
                                return
                            });
                    })
                        .catch(function (error) {
                            self.snackbarText = error;
                            self.snackbar = true;
                            return
                        });
                } else {
                    this.$router.replace('dashboard');
                }
            },
        },

        computed: {
            buttonIsDisabled: function () {
                return this.imageUpload == null;
            }
        }
    }
</script>

